
import { computed, defineComponent, ref } from 'vue'
import { useStore } from '@/store'
import router from '@/router'
import { useRoute } from 'vue-router'
import { pathMapBreadcrumbs, pathMapToMenu } from '@/utils/map-menus'

export default defineComponent({
  props: {
    collapse: {
      type: Boolean,
      default: false
    }
  },
  setup() {
    const store = useStore()
    const userMenus = computed(() => store.state.login.userMenus)
    const handleMenuItemClick = (item: any) => {
      console.log('菜单点击', item.url)
      router.push({
        path: item.url ?? '/not-found'
      })
    }

    // 根据当前路径拿usermenu的id
    const route = useRoute()
    const menu = pathMapToMenu(userMenus.value, route.path)
    // console.log(menu.id, typeof (menu.id + ''))
    const defaulValue = ref(menu.id + '')

    return { userMenus, handleMenuItemClick, defaulValue }
  }
})
